    mCalc(){
        var _this = this; // forEachthisthis
        this.packs.forEach(function(pack){
          pack.myPrice = _this.pJewel * pack.items.Jewel // 
                      + _this.pTicketLuxury * pack.items.TicketLuxury // 
                      + _this.pTicketRegular * pack.items.TicketRegular // 
                      + _this.pMoldCompany * pack.items.MoldCompany // 
                      + _this.pMoldHigh * pack.items.MoldHigh // 
                      + _this.pMoldMiddle * pack.items.MoldMiddle // 
                      + _this.pNikeSelect * pack.items.NikeSelect // 
                      + _this.pCostume * pack.items.Costume // 
                      + _this.pCaseCredit * pack.items.CaseCredit // 
                      + _this.pCaseCore * pack.items.CaseCore // 
                      + _this.pCaseBattle * pack.items.CaseBattle // 
                      + _this.pCredit * pack.items.Credit // 1k
                      + _this.pCore * pack.items.Core // 
                      + _this.pManualSkill1 * pack.items.ManualSkill1 // 
                      + _this.pManualSkill2 * pack.items.ManualSkill2 // 
                      + _this.pManualSkill3 * pack.items.ManualSkill3 // 
                      + _this.pManualBurst1 * pack.items.ManualBurst1 // 
                      + _this.pManualBurst2 * pack.items.ManualBurst2 // 
                      + _this.pManualBurst3 * pack.items.ManualBurst3 // 
                      + _this.pManualAttribute * pack.items.ManualAttribute // 
                      + _this.pCompanyArms * pack.items.CompanyArms // 
                      + _this.pCustomModule * pack.items.CustomModule // 
                      + _this.pModuleHigh * pack.items.ModuleHigh // 
                      + _this.pModule * pack.items.Module // 
                      + _this.pEquipmentT9 * pack.items.EquipmentT9 // T9
                      + _this.pEquipmentT8 * pack.items.EquipmentT8 // T8
                      + _this.pEquipmentT7 * pack.items.EquipmentT7 // T7
                      + _this.pFavorability500 * pack.items.Favorability500 // VIP
                      + _this.pFavorability100 * pack.items.Favorability100 // 
                      + _this.pFavorability50 * pack.items.Favorability50 // 
                      + Math.max(_this.pCaseCredit * 3
                                , _this.pCaseCore
                                , _this.pCaseBattle
                                ) * pack.items.SupplyCG1 // 
                                
                      + Math.max(_this.pManualSkill1 * 7
                                , _this.pManualSkill2 * 3
                                , _this.pManualSkill3
                                ) * pack.items.ManualSkillSelect // 

                      + Math.max(_this.pManualBurst1 * 9
                                , _this.pManualBurst2 * 7
                                , _this.pManualBurst3 * 3
                                ) * pack.items.ManualBurstSelect // 

                      + Math.max(_this.pCaseCredit * 30
                                , _this.pCaseCore * 10
                                , _this.pCaseBattle * 10
                                , _this.pManualSkill1 * 28
                                , _this.pManualSkill2 * 20
                                , _this.pManualSkill3 * 8
                                , _this.pManualBurst1 * 14
                                , _this.pManualBurst2 * 10
                                , _this.pManualBurst3 * 4
                                ) * pack.items.Supply30Day // 30-DAY

                      + Math.max(_this.pCaseCredit * 25 + _this.pCaseCore * 25 + _this.pCaseBattle * 25
                                , _this.pCaseCredit * 150
                                , _this.pCaseCore * 50
                                , _this.pCaseBattle * 50
                                ) * pack.items.CustomPack1 // (1)
                      + Math.max(_this.pManualSkill1 * 200
                                , _this.pManualSkill2 * 140
                                , _this.pManualBurst1 * 100
                                , _this.pManualBurst2 * 70
                                ) * pack.items.CustomPack1 // (2)
                      
                      + Math.max(_this.pCompanyArms * 200
                                , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                                , _this.pCaseCredit * 300
                                , _this.pCaseCore * 100
                                , _this.pCaseBattle * 100
                                ) * pack.items.CustomPack2 // (1)
                      + Math.max(_this.pCustomModule * 2
                                , _this.pManualSkill1 * 250
                                , _this.pManualSkill2 * 180
                                , _this.pManualBurst1 * 125
                                , _this.pManualBurst2 * 90
                                ) * pack.items.CustomPack2 // (2)
                      + Math.max(_this.pManualSkill1 * 360
                                , _this.pManualSkill2 * 250
                                , _this.pManualSkill3 * 100
                                , _this.pManualBurst1 * 180
                                , _this.pManualBurst2 * 125
                                , _this.pManualBurst3 * 50
                                ) * pack.items.CustomPack2 // (3)
                      
                      + Math.max(_this.pCompanyArms * 200
                                , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                                , _this.pCaseCredit * 300
                                , _this.pCaseCore * 100
                                , _this.pCaseBattle * 100
                                ) * pack.items.CustomPack3 // (1)
                      + Math.max(_this.pCustomModule * 3
                                , _this.pManualSkill1 * 300
                                , _this.pManualSkill2 * 220
                                , _this.pManualBurst1 * 150
                                , _this.pManualBurst2 * 110
                                ) * pack.items.CustomPack3 // (2)
                      + Math.max(_this.pManualSkill1 * 360
                                , _this.pManualSkill2 * 250
                                , _this.pManualSkill3 * 100
                                , _this.pManualBurst1 * 180
                                , _this.pManualBurst2 * 125
                                , _this.pManualBurst3 * 50
                                ) * pack.items.CustomPack3 // (3)
                      + Math.max(_this.pTicketRegular * 30
                                , _this.pEquipmentT9 * 1
                                ) * pack.items.CustomPack3 // (3)
                      ;

          pack.degree = pack.myPrice / pack.price * 100;  // 
        })
      },

    /*mInputClose(){
      this.isInputOpen = false;
      this.isInputClose = true;
    },

    mInputOpen(){
      this.isInputOpen = true;
      this.isInputClose = false;
    },*/

    mDelSearchWord(){
      this.searchWord = "";
    },

    mSortBy(key) {
      this.sort_key === key
      ? (this.sort_asc = !this.sort_asc)
      : (this.sort_asc = true)
      this.sort_key = key
    },

    mAddClass(key) {
      return {
        asc: this.sort_key === key && this.sort_asc,
        desc: this.sort_key === key && !this.sort_asc
      }
    }
  },

  computed: {
    packs_sort() {
      // 
      if(this.sort_key != "") {
        let set = 1;
        this.sort_asc ? (set = 1) : (set = -1)
        this.packs.sort((a, b) => {
          if (a[this.sort_key] < b[this.sort_key]) return -1 * set;
          if (a[this.sort_key] > b[this.sort_key]) return 1 * set;
          return 0;
        })
        //return this.packs
      } else {
        //return this.packs
      }

      // 
      var data = this.packs;
      var filterWord = this.searchWord && this.searchWord.toLowerCase().trim();
      // FLASH SALE
      if(!this.checkFlashSale){
        data = data.filter(function (row) {
          return row['division'].indexOf("FLASH SALE") == -1;
        });
      }
      //
      if(filterWord) {
          data = data.filter(function (row) {
              return Object.keys(row).some(function (key) {
                  return String(row[key]).toLowerCase().indexOf(filterWord) > -1
              })
          })
      }
      return data
    }
  },
}
</script>

